import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export const store = new Vuex.Store({
	state: {
		visible: true,
		background: "transparent",
		padNavBar: { paddingLeft: "20px", paddingRight: "20px" }
	},
	mutations: {
		showNavAndFooter(state, payload) {
			if (!payload) {
				state.visible = payload;
			} else {
				state.visible = true;
			}
		},
		changeBackground(state, payload) {
			state.background = payload;
		},
		changeNavBar(state, payload) {
			if (payload) {
				state.padNavBar = payload;
			}
		}
	}
});



