<template>
    <div id="brand" style="overflow-x:hidden">
        <form>
            <div class="row">
                <div class="reserve">
                    <div class="col-md-5">
                        <div class="col-md-12">
                                <div class="intern_sec">
                                    <h3 style="color:#20a5de">Learning Beyond Borders</h3>
                                    <p>We are re-writting the narrative of what learning truly <br>really is. Signup to be in the know</p>
                                </div>
                            <div class="select">
                                <div style="font-size:20px;margin-top:10px;margin-bottom:10px;color:#AEACAC">
                                    <P style="margin-top:10px;margin-bottom:10px">What do we call you?</P>
                                    <input type="text" placeholder="Awesome person" style="color:black" ref="name">
                                </div>
                                 <div style="font-size:20px;">
                                    <P style="margin-top:10px;margin-bottom:10px; color:#AEACAC">Your email address?</P>
                                    <input type="email" placeholder="speacialperson@gmail.com" ref="email">
                                </div>
                                <div style="font-size:20px;">
                                    <P style="margin-top:10px;margin-bottom:10px;color:#AEACAC">Your phone number?</P>
                                    <input type="number" placeholder="08100000000" style="color:black" ref="phone">
                                </div>
                            </div>
                                
                        </div>
                    </div>
                    <div class="col-md-7">
                        <div class="img_think">
                            <img src="../assets/images/learning.svg" alt="" >
                        </div>
                    </div>
                </div>

            </div>
            <div class="center " style="padding-bottom:30px;">
                <button class="button" id="l0gin" style="margin-top:20px" @click.prevent="sendData($refs.name, $refs.email,$refs.phone)">Submit</button>
            </div>
        </form>

    </div>
</template>
<style scoped>
h3 {
	font-size: 20px;
}
.intern_sec{
    padding-top:20px;
    text-align:center;
}
#brand {
	margin-top: 50px;
	padding-left: 50px;
	padding-right: 50px;
}
.flex {
	display: flex;
	flex-direction: column;
}
.button {
	height: 52px;
	width: 230px;
	border-radius: 26px;
	background-color: #20a5de;
	box-shadow: 0 19px 23px -22px rgba(0, 0, 0, 0.5);
	color: white;
	font-size: 18px;
}
.button:hover {
	background-color: white;
	color: #20a5de;
	border: 1px solid #20a5de;
}
.select{
    padding-top:20px;
}
.center {
	margin: 0 auto;
	display: block;
	text-align: center;
}
.button_budget {
	border: none;
	background-color: transparent;
}
.button_budget:focus {
	border: none;
	background-color: #20a5de;
	font-size: 30px;
	border-radius: 10px;
	padding-top: 20px;
	padding-bottom: 20px;
}
label{
    border: 1px solid #AEACAC;
    width: 100%;
    text-align: inherit;
}

button:focus {
	border: none;
	outline: none;
}
img{
    width:100%; 
    height:500px;
}
input {
	width: 100%;
	border: none;
	border-bottom: 2px solid rgb(174, 172, 172);
	margin-bottom: 20px;
	background-color:transparent;
}
input:focus {
	border: none;
	outline: none;
	border-bottom: 3px solid #20a5de;
}
input,
input::-webkit-input-placeholder {
	font-size: 15px;
	
}


@media only screen and (min-device-width: 320px) {
	h3 {
		font-size: 22px;
		font-family: "TitilliumBd";
	}
	#brand {
		padding-left: 10px;
		padding-right: 10px;
	}
	.reserve {
		display: flex;
		flex-direction: column-reverse;
	}
	.img_think {
		padding-top: 40px;
		
	}
    img{
        height: 250px;
    }
}


@media only screen and (min-device-width: 768px) {
    .reserve {
		display: flex;
		flex-direction: column-reverse;
	}
    img{
        height:500px;
    }
    h3{
        font-size:44px;
    }
    .select{
        padding-left:165px;
        padding-right:165px;
    }
}

@media only screen and (min-device-width: 1024px) {
	.reserve {
		display: block;
	}
	textarea {
		height: 204px;
		margin-bottom: 30px;
	}
	.button_budget {
		margin-top: 20px;
		margin-bottom: 20px;
	}
	h3 {
		font-size: 47px;
        padding-top:30px;
	}
	.reserve {
		padding-left: 40px;
		padding-right: 40px;
	}
    .select{
        padding-left: 57px;
        padding-right: 57px;
    }
	
