<template>
    <div style="overflow-x: hidden">
        <div class="design-body">
            <div class="row body-heading">
                <div class="col-sm-12 col-md-12 col-lg-12 heading_text">
                    <p style="margin: 0; paddingLeft: 15%; paddingRight: 15%;">Design and Development</p>
                </div>
            </div>
            <div class="row div1">
                <div class="col-sm-6 col-md-6 col-lg-6">
                    <div class="body_text">
                        <p>With our array of ideas and solutions, we build platforms that delivers results because we know you want results. Our collaborative, user-centric approach to design ensures we can quickly prototype solutions to challenging problems, then test and refine the creative to ensure that the content architecture and user experience are fully optimised.</p>
                    </div>
                </div>
                <div class="col-sm-6 col-md-6 col-lg-6">
                    <div class="icon-bg">
                        <div class="video-cont">
                            <video src="../assets/videos/full.mp4" playsinline autoplay muted loop></video>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row btns-tray">
                <div class="col-sm-12 col-md-12 col-lg-12 design-navbtns">
                    <div class="col-4 active-link" @click="showMobile()" ref="mobileNavBtn">
                        <p>Mobile Apps</p>
                    </div>
                    <div class="col-4" @click="showWeb()" ref="webNavBtn">
                        <p>Web Development</p>
                    </div>
                    <div class="col-4" @click="showGame()" ref="gameNavBtn">
                        <p>Brand Management</p>
                    </div>
                </div>
            </div>
            <div class="row design-content">
                <div class="common-content" v-show="isMobile">
                    <div class="col-sm-12 col-md-12 col-lg-12 text-before-video">
                        <p>We look beyond the cluster of existing mobile applications, to develop apps that are innovative, easily-accessible, utility-driven and user-friendly, taking into consideration its unique functionality that helps it stand out and also deliver its promised value.</p>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-12">
                        <div class="icon-bg">
                            <div class="video-cont">
                                <video src="../assets/videos/mobile-apps.mp4" playsinline autoplay muted loop></video>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="common-content" v-show="isWeb">
                    <div class="col-sm-12 col-md-12 col-lg-12 text-before-video">
                        <p>We build websites that are more than just “websites.” For us, responsive design, seamless functionality and a rich user-experience are priority than bogus words and heavy image layouts.</p>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-12">
                        <div class="icon-bg">
                            <div class="video-cont">
                                <video src="../assets/videos/web-dev.mp4" playsinline autoplay muted loop></video>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="common-content" v-show="isGame">
                    <div class="col-sm-12 col-md-12 col-lg-12 text-before-video">
                        <p>We help your brand or business find its voice that resonates accurately with its desired target audience. From brand perception and customer experience to brand engagement and customer loyalty, our creatives would walk with you, hand-in-hand, through all processes involved in developing and defining your unique brand identity.</p>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-12">
                        <div class="icon-bg">
                            <div class="video-cont">
                                <video src="../assets/videos/brand development.mp4" playsinline autoplay muted loop></video>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row growth-div">
                <div class="col-sm-12 col-md-12 col-lg-12 growth">
                    <h4>Let's Grow You</h4>
                </div>
                <div class="col-sm-12 col-md-12 col-lg-12 text-center growth-text">
                    <p>A time will come when you will need the ultimate creative solution. Or a fresh new brand perspective. Or a chance to save the world. Or to paint with the colours of the wind. Or just to be showered with a new level of awesomeness</p>
                </div>
                <div class="col-sm-12 col-md-12 col-lg-12 text-center">
                    <router-link to="/brand" tag="button" class="btn">
                        GET STARTED
                    </router-link>
                    <!-- <button class="btn">GET STARTED</button> -->
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
	data() {
		return {
			isMobile: true,
			isWeb: false,
			isGame: false
		};
	},
	methods: {
		showWeb() {
			const navBtns = document.querySelectorAll(".design-navbtns div");
			this.isGame = false;
			this.isMobile = false;
			this.isWeb = true;
			for (let i = 0; i < navBtns.length; i++) {
				const element = navBtns[i];
				if (element.classList.contains("active-link")) {
					element.classList.remove("active-link");
					this.$refs.webNavBtn.classList.add("active-link");
				}
			}
		},
		showGame() {
			const navBtns = document.querySelectorAll(".design-navbtns div");
			this.isMobile = false;
			this.isWeb = false;
			this.isGame = true;
			for (let i = 0; i < navBtns.length; i++) {
				const element = navBtns[i];
				if (element.classList.contains("active-link")) {
					element.classList.remove("active-link");
					this.$refs.gameNavBtn.classList.add("active-link");
				}
			}
		},
		showMobile() {
			const navBtns = document.querySelectorAll(".design-navbtns div");
			this.isWeb = false;
			this.isGame = false;
			this.isMobile = true;
			for (let i = 0; i < navBtns.length; i++) {
				const element = navBtns[i];
				if (element.classList.contains("active-link")) {
					element.classList.remove("active-link");
					this.$refs.mobileNavBtn.classList.add("active-link");
				}
			}
		}
	},
	beforeMount() {
		this.$store.commit("showNavAndFooter", true);
		let background = { backgroundColor: "#fcb640" };
		let navBarStyle;
		if (window.innerWidth >= 900) {
			navBarStyle = { paddingLeft: "15%", paddingRight: "15%" };
		} else {
			if (window.innerWidth <= 500) {
				navBarStyle = { paddingLeft: "10px", paddingRight: "10px" };
			} else {
				navBarStyle = { paddingLeft: "20px", paddingRight: "20px" };
			}
		}

		this.$store.commit("changeBackground", background);
		this.$store.commit("changeNavBar", navBarStyle);
	}
};
</script>
<style scoped>
.design-body {
	font-size: 18px;
	color: #9a9a9a;
}
.body-heading {
	margin-top: 50px;
}
.heading_text {
	height: 50px;
	display: flex;
	align-items: center;
	padding-left: 25px;
	font-size: 20px;
	font-family: "TitilliumBd";
	color: #20a5de;
}
.body_text {
	padding-left: 10px;
	padding-right: 10px;
}
.div1 {
	display: flex;
	flex-direction: column-reverse;
}
.design-navbtns {
	display: flex;
	align-items: center;
	justify-content: center;
	color: white;
	height: 80px;
	background-color: #fce9ca;
}
.design-navbtns p {
	margin: 0;
}
.design-navbtns div:hover {
	background-color: rgb(252, 182, 64);
}
.btns-tray {
	margin-top: 20px;
}
.design-navbtns div {
	padding-top: 10px;
	padding-bottom: 10px;
	text-align: center;
	padding-left: 5px;
	padding-right: 5px;
	cursor: pointer;
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	/* font-size: 20px; */
	font-family: "TitilliumBd";
}
.common-content {
	margin-top: 30px;
	padding-left: 10px;
	padding-right: 10px;
}
.growth {
	margin-top: 30px;
	text-align: center;
	font-size: 25px;
	font-family: "TitilliumBd";
	color: #20a5de;
}
.growth-div {
	margin-bottom: 30px;
	padding-left: 10px;
	padding-right: 10px;
}
.growth-div button {
	margin-top: 30px;
	height: 52px;
	width: 230px;
	border-radius: 26px;
	background-color: #fcb640;
	box-shadow: 0 19px 23px -22px rgba(0, 0, 0, 0.5);
	color: white;
	font-family: "TitilliumBd";
}
video {
	height: 250px;
	width: 100%;
	object-fit: scale-down;
	object-position: center;
}
.active-link {
	background-color: #fcb640;
}
@media only screen and (min-device-width: 600px) {
	.div1 {
		display: flex;
		flex-direction: row;
		align-items: center;
	}
	.body-heading {
		margin-top: 70px;
	}
	.heading_text {
		color: white;
		background-color: #20a5de;
		height: 70px;
	}
	.common-content {
		display: flex;
		align-items: center;
		justify-content: center;
	}
}
@media only screen and (min-device-width: 900px) {
	.design-body {
		font-size: 20px;
	}
	.div1,
	.growth-text {
		padding-left: 15%;
		padding-right: 15%;
	}
	video {
		object-fit: scale-down;
		height: 350px;
		width: 100%;
	}
	.common-content {
		display: flex;
		flex-direction: column;
		padding: 0;
	}
	.text-before-video {
		padding-left: 25%;
		padding-right: 25%;
		height: 200px;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
	}
	.navbar-top {
		padding-left: 15%;
		padding-right: 15%;
	}
}
</style>




