<template>
	<div class="footer" v-show="visible">
		<!-- <p>Footer component</p> -->
		<div class="footer-logo">
			<img src="../assets/images/codekago-blue.png" alt="Codekago Logo">
			<!-- <hr> -->
		</div>
		<div class="footer-contact-us">
			<h4>Contact Us</h4>
			<div>
				<a href="mailto:info@codekago.com">info@codekago.com</a>
				<p>86, Atiku Abubakar way, Uyo, Akwa Ibom state.</p>
			</div>
		</div>
		<div class="reach-out">
			<p>Call Us</p>
			<p>+234 70 3927 8132</p>
			<div class="social-media">
				<a href="https://www.instagram.com/codekago" class="fa fa-instagram"></a>
				<a href="https://www.twitter.com/codekago" class="fa fa-twitter"></a>
				<a href="https://www.linkedin.com/codekago" class="fa fa-linkedin"></a>
				<a href="https://www.facebook.com/codekago.com" class="fa fa-facebook"></a>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	computed: {
		visible() {
			return this.$store.state.visible;
		}
	}
};
</script>
<style>
.footer {
	/* display: none; */
	padding-top: 15px;
	padding-bottom: 15px;
	padding-left: 20px;
	padding-right: 20px;
	font-size: 18px;
	background-image: url("../assets/images/map.svg");
	background-color: rgba(0, 0, 0, 0.6);
	background-position: center;
	background-size: cover;
	color: white;
	min-height: 30vh;
}
.footer-logo {
	padding-top: 10px;
	padding-bottom: 10px;
	text-align: center;
}
.footer-logo img {
	width: 170px;
}
.footer-contact-us {
	height: 215px;
	padding-top: 20px;
	padding-bottom: 20px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}
.footer-contact-us a {
	color: #dddddd;
}
.footer-contact-us h4 {
	font-size: 18px;
	color: #30aadf;
	font-weight: bold;
	font-family: "TitilliumBd";
}
.footer-contact-us p {
	margin-top: 10px;
}
.social-media {
	display: flex;
	justify-content: center;
}
.social-media a {
	margin-top: 15px;
	color: white;
	margin-left: 20px;
}
@media only screen and (min-device-width: 600px) {
	.footer {
		/* display: none; */
		display: grid;
		grid-template-columns: 35% auto auto;
		align-items: baseline;
		justify-items: baseline;
	}
	.footer-contact-us {
		padding-left: 10px;
		padding-right: 10px;
	}
	.reach-out {
		display: flex;
		flex-direction: column;
		align-items: baseline;
	}
	.social-media a {
		margin-left: unset;
		margin-right: 30px;
	}
}
@media only screen and (min-device-width: 900px) {
	.footer {
		grid-template-columns: 40% auto auto;
		padding-left: 50px;
		padding-right: 50px;
		padding-top: 30px;
		padding-bottom: 30px;
	}
}
/* .footer {
  display: none;
} */
</style>




