<template>
	<div class="sidebar_cont">
		<div class="sidebar" v-show="visible">
			<div class="navbar-top" :style="padNavBar">
				<div class="logo-icon" @click="goHome">
					<img src="../assets/images/codekago-blue.png" alt="Codekago logo">
				</div>
				<div id="toggle-nav-btn" @click='showNav'>
					<!-- <i class="fa fa-bars"></i> -->
					<img src="../assets/images/nav.svg" alt="navigation icon">
				</div>
			</div>
			<div class="nav-slide" ref="navScreen" :style="background">
				<div class="nav-content">
					<router-link to="/design" tag="button" @click.native="hideNav">Design and Development</router-link>
					<router-link to="/education" tag="button" @click.native="hideNav">Education</router-link>
					<router-link to="/networking" tag="button" @click.native="hideNav">Networking</router-link>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	computed: {
		visible() {
			return this.$store.state.visible;
		},
		background() {
			return this.$store.state.background;
		},
		padNavBar() {
			return this.$store.state.padNavBar;
		}
	},
	methods: {
		showNav() {
			let params = this.$refs.navScreen;
			if (!params.classList.contains("nav-open")) {
				params.classList.add("nav-open");
				params.style.zIndex = 5;
			} else {
				params.classList.remove("nav-open");
			}
		},
		hideNav() {
			let params = this.$refs.navScreen;
			params.classList.remove("nav-open");
		},
		goHome() {
			this.$router.push("/");
		}
	}
};
</script>
<style>
.navbar-top {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
	z-index: 10;
	padding: 10px;
	background-color: white;
}
.nav-slide {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	margin-top: 5%;
	background-color: rgba(0, 0, 0, 0.75);
	padding-top: 50px;
	height: 100vh;
	padding-left: 25px;
	padding-right: 25px;
	padding-bottom: 50px;
	display: flex;
	align-items: flex-end;
	justify-content: center;
	transition: 1s ease-in-out;
	opacity: 0;
	visibility: hidden;
}
.nav-content {
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	color: white;
	/* border: 1px solid white; */
}
.nav-open {
	visibility: visible;
	opacity: 1;
}
.logo-icon img {
	width: 100px;
}
.nav-content button {
	border: 1px solid #ffffff;
	height: 32px;
	width: 188px;
	border-radius: 5px;
	font-family: "Titillium";
	font-size: 14px;
	line-height: 18px;
	margin-bottom: 5px;
	background: transparent;
	color: white;
	text-align: left;
	outline: none;
}

@media only screen and (min-device-width: 600px) {
	.nav-slide {
		align-items: center;
		flex-direction: column;
		justify-content: center;
		width: auto;
		right: 0;
		left: unset;
		transform: translateX(100%);
		margin-top: 3%;
	}
	.nav-open {
		transform: translateX(0%);
	}
	.nav-content {
		width: unset;
		height: 25%;
		justify-content: space-evenly;
	}
	.nav-content button {
		height: 45px;
		width: 280px;
		font-size: 19px;
		padding-left: 20px;
		padding-top: 5px;
		padding-bottom: 5px;
	}
	.navbar-top {
		height: 70px;
		padding-left: 20px;
		padding-right: 20px;
	}
	.logo-icon img {
		width: 155px;
	}
}
</style>




