<template>
    <div style="overflow-x: hidden">
        <div class="error-page">
            <!-- <div class="page-404">
                <p>404</p>
            </div> -->
            <div class="img-plead">
                <img src="../assets/images/Icon.svg" alt="">
            </div>
            <div class="text-and-image">
                <div class="text">
                    <h4>Oops</h4>
                    <p>It seems you’ve opened a page that we don’t have anymore. We’re totally sorry about that</p>
                    <router-link to="/" tag="button" class="btn">
                        HOME
                    </router-link>
                    <!-- <button class="btn">HOME</button> -->
                </div>
                <div class="svg-icon"></div>
            </div>

        </div>
    </div>
</template>
<script>
export default {
	beforeMount() {
		this.$store.commit("showNavAndFooter", true);
		let background = { backgroundColor: "#20A5DE" };
		let navBarStyle;
		if (window.innerWidth <= 500) {
			navBarStyle = { paddingLeft: "10px", paddingRight: "10px" };
		} else {
			navBarStyle = { paddingLeft: "20px", paddingRight: "20px" };
		}
		this.$store.commit("changeBackground", background);
		this.$store.commit("changeNavBar", navBarStyle);
	}
};
</script>
<style>
.error-page {
	margin-top: 45px;
	height: 70vh;
	background-image: url(../assets/images/bg.svg);
	/* object-fit: cover;
	object-position: center; */
	color: white;
	background-position: center;
	background-size: cover;
	position: relative;
}
.text {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	position: absolute;
	top: 15%;
	height: 65%;
	padding-left: 10%;
	padding-right: 10%;
}
h4 {
	font-size: 35px;
	font-family: "TitilliumBd";
}
.text p {
	font-size: 18px;
}
.text button {
	width: 50%;
	border-radius: 26px;
	background-color: #fcb640;
	box-shadow: 0 19px 23px -22px rgba(0, 0, 0, 0.5);
	color: white;
	font-size: 14px;
	font-family: "Titillium";
	font-weight: bold;
}
.img-plead {
	position: absolute;
	bottom: 0;
	right: -40px;
}
.img-plead img {
	height: 250px;
	width: 100%;
}
@media only screen and (min-device-width: 600px) {
	.text p {
		width: 300px;
		font-size: 16px;
	}
	.img-plead {
		right: 55px;
	}
	.text {
		height: 50%;
		top: 25%;
	}
}
@media only screen and (min-device-height: 600px) {
	.error-page {
		height: 65vh;
		margin-top: 3%;
	}
}
@media only screen and (min-device-width: 1000px) {
	.text p {
		font-size: 18px;
		width: 450px;
	}
}
@media only screen and (max-device-width: 320px) {
	.text {
		top: 10%;
		height: 60%;
	}
}
</style>



