<template>
    <div id="brand" style="overflow-x:hidden">
        <form>
            <div class="row">
                <div class="reserve">
                    <div class="col-md-6">
                        <div class="col-md-12">
                            <h3 style="color:#AEACAC">First things first</h3>

                            <div style="font-size:20px;margin-top:10px;margin-bottom:10px">
                                <P style="margin-top:10px;margin-bottom:10px;color:#AEACAC">What do we call you?</P>
                                <input type="text" placeholder="Awesome Person" ref="name" style="text-transform:uppercase">
                            </div>
                            <div style="font-size:20px;margin-top:10px;margin-bottom:10px">
                                <P style="margin-top:10px;margin-bottom:10px; color:#AEACAC">Your email address?</P>
                                <input type="email" placeholder="speacialperson@gmail.com" ref="email">
                            </div>
                            <div style="font-size:20px;margin-top:10px;margin-bottom:10px;color:#AEACAC">
                                <P style="margin-top:10px;margin-bottom:10px">Your phone number?</P>
                                <input type="number" placeholder="08100000000" ref="phone">
                            </div>
                            <div style="font-size:20px;margin-top:10px;margin-bottom:10px">
                                <p style="color:#AEACAC">What's your budget like?</p>
                                <div class="radio">
                                    <label class="radio-inline" style="color:#AEACAC"><input type="radio" name="optradio" value="200k - 300k" ref="budget" style="color:#AEACAC">N200k - N300k</label>
                                    <label class="radio-inline" style="color:#AEACAC"><input type="radio" name="optradio" value="350k - 500k" ref="budget" style="color:#AEACAC">N350k - N500k</label>
                                    <label class="radio-inline" style="color:#AEACAC"><input type="radio" name="optradio" value="500k+" ref="budget" style="color:#AEACAC">N500k & above </label>
                                </div>
                            </div>
                            <div>
                                <h3 style="color:#AEACAC">Let's hear that amazing idea</h3>

                                <textarea class="form-control area" rows="5" id="comment" placeholder="Let's hear that brilliant idea" ref="idea"></textarea>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="img_think">
                            <img src="../assets/images/first things.svg" alt="" style="width:100%">
                        </div>
                    </div>
                </div>

            </div>
            <div class="center " style="padding-bottom:30px;">
                <button class="button" id="l0gin" style="margin-top:20px" @click.prevent="getValue">NEXT</button>
            </div>
        </form>

    </div>
</template>
