<script src="https://unpkg.com/sweetalert/dist/sweetalert.min.js"></script>

<script>
let submit = document.querySelector("#l0gin");

import axios from "axios";
export default {
	data() {
		return {
			checkedRadio: ""
		};
	},
	methods: {
		getValue() {
			let name = this.$refs.name.value,
				email = this.$refs.email.value,
				phone = this.$refs.phone.value,
				idea = this.$refs.idea.value,
				budget = this.$refs.budget.value;
			console.log(budget);

			if (
				name == "" ||
				email == "" ||
				phone == "" ||
				idea == "" ||
				budget == ""
			) {
				console.log("empty");
				swal(
					"Ooops...",
					"To serve you better all input fields are required",
					"error"
				);
			} else {
				console.log(name, email, phone, idea, budget);
				axios({
					method: "post",
					headers: {
						"Content-Type": "application/x-www-form-urlencoded"
					},
					url: "http://codekago.com/api/v1/idea/add",
					data: {
						name,
						email,
						phone,
						idea,
						budget
					}
				}).then(res => {
					if (res.status == 200) {
						location.href = "/thank";
					} else {
						swal(
							"Ooops...",
							"An error occurred please fill in required information",
							"error"
						);
					}
				});
			}
		}
	},
	beforeMount() {
		this.$store.commit("showNavAndFooter", true);
		let background = { backgroundColor: "rgba(0,0,0,0.5)" };
		let navBarStyle;
		if (window.innerWidth <= 500) {
			navBarStyle = { paddingLeft: "10px", paddingRight: "10px" };
		} else {
			navBarStyle = { paddingLeft: "20px", paddingRight: "20px" };
		}
		this.$store.commit("changeBackground", background);
		this.$store.commit("changeNavBar", navBarStyle);
	}
};
</script>



