	for (const key in details) {
		if (details.hasOwnProperty(key)) {
			const element = details[key];
			urlEncodedDataPairs.push(
				encodeURIComponent(key) + "=" + encodeURIComponent(element)
			);
		}
	}
	urlEncodedData = urlEncodedDataPairs.join("&").replace(/%20/g, "+");
	const params = {
		method: "POST",
		headers: { "Content-Type": "application/x-www-form-urlencoded" },
		body: urlEncodedData
	};
	fetch("https://codekago.com/api/v1/networking/add", params)
		.then(res => {
			if (res.ok) {
				return res.json();
			}
		})
		.then(feedback => swal(feedback.message))
		.catch(e => swal(feedback.message));
}

export default {
	data() {
		return {
			isHackathon: true,
			isHangouts: false,
			isWorkshops: false
		};
	},
	methods: {
		showHackathon() {
			const navBtns = document.querySelectorAll(".network-navbar div");
			this.isHangouts = false;
			this.isWorkshops = false;
			this.isHackathon = true;
			for (let i = 0; i < navBtns.length; i++) {
				const element = navBtns[i];
				if (element.classList.contains("active-link")) {
					element.classList.remove("active-link");
					this.$refs.hackathonNav.classList.add("active-link");
				}
			}
		},
		showHangouts() {
			const navBtns = document.querySelectorAll(".network-navbar div");
			this.isHackathon = false;
			this.isWorkshops = false;
			this.isHangouts = true;
			for (let i = 0; i < navBtns.length; i++) {
				const element = navBtns[i];
				if (element.classList.contains("active-link")) {
					element.classList.remove("active-link");
					this.$refs.hangoutNav.classList.add("active-link");
				}
			}
		},
		showWorkshops() {
			const navBtns = document.querySelectorAll(".network-navbar div");
			this.isHackathon = false;
			this.isHangouts = false;
			this.isWorkshops = true;
			for (let i = 0; i < navBtns.length; i++) {
				const element = navBtns[i];
				if (element.classList.contains("active-link")) {
					element.classList.remove("active-link");
					this.$refs.workshopNav.classList.add("active-link");
				}
			}
		},
		addToMailList(params1, params2) {
			let name = params1.value;
			let email = params2.value;
			if (!email || !name) {
				swal({
					icon: "error",
					title: "Empty input fields",
					text: "Please fill in all required fields"
				});
			} else {
				let data = { name, email };
				postData(data);
				params1.value = "";
				params2.value = "";
			}
		},
		nextScreen(params) {
			params.style.display = "initial";
			params.style.position = "absolute";
			params.style.top = 0;
			params.style.left = 0;
			params.style.width = "100%";
			params.style.height = "105%";
			params.style.backgroundColor = "white";
			params.style.zIndex = 2;
			this.$refs.shiftDown.style.marginTop = "30px";
			this.$refs.shiftDown.style.transition = "1s ease";
		},
		closeScreen(params) {
			params.style.display = "none";
			this.$refs.shiftDown.style.marginTop = "0px";
			this.$refs.shiftDown.style.transition = "1s ease";
		},
		showBio(params) {
			params.style.transform = "translateX(0%)";
		},
		unshowBio(params) {
			params.style.transform = "translateX(103%)";
		}
	},
	beforeMount() {
		this.$store.commit("showNavAndFooter", true);
		let background = { backgroundColor: "rgb(146, 199, 148)" };
		let navBarStyle;
		if (window.innerWidth <= 500) {
			navBarStyle = { paddingLeft: "10px", paddingRight: "10px" };
		} else {
			navBarStyle = { paddingLeft: "20px", paddingRight: "20px" };
		}
		this.$store.commit("changeBackground", background);
		this.$store.commit("changeNavBar", navBarStyle);
	}
};
