<script>
export default {
	data() {
		return {
			msg:
				"Through our Offline Programs, we are creating an opportunity for Professionals and beginners alike, who are looking for an immersive, one-on-one learning experience. Our office spaces offer that perfect environment you need to learn.",
			msg2:
				"You can become a certified professional by taking our online courses from the convenience of your space and at your own pace. Our courses are verified and approved by various professional and educational bodies and are accepted as a basis of employment by top firms across the country.",
			internship: false,
			academy: true,
			bootcamp: false,
			internship_impact: false,
			academy_impact: true,
			bootcamp_impact: false,
			active_link: false
		};
	},

	methods: {
		apply() {
			location.href = "https://bit.ly/2sUtzbM";
		},
		intern() {
			location.href = "/intern";
		},
		visit() {
			location.href = "/apply";
		},
		showEducation() {
			const navbar = document.querySelectorAll(".navbar div");
			for (let i = 0; i < navbar.length; i++) {
				const element = navbar[i];
				if (element.classList.contains("active_link")) {
					element.classList.remove("active_link");
					this.$refs.academy_link.classList.add("active_link");
				}
			}
		},
		showEducation_2() {
			const navbar = document.querySelectorAll(".navbar div");
			for (let i = 0; i < navbar.length; i++) {
				const element = navbar[i];
				if (element.classList.contains("active_link")) {
					element.classList.remove("active_link");
					this.$refs.internship_link.classList.add("active_link");
				}
			}
		},
		showEducation_3() {
			const navbar = document.querySelectorAll(".navbar div");
			for (let i = 0; i < navbar.length; i++) {
				const element = navbar[i];
				if (element.classList.contains("active_link")) {
					element.classList.remove("active_link");
					this.$refs.bootcamp_link.classList.add("active_link");
				}
			}
		}
	}
};
</script>




